


















































































export default {
	data() {
		return {
			alertModal : false, // 查看失败原因弹窗,
			list : [
				{ name : '推荐名称一'},
				{ name : '推荐名称二'},
				{ name : '推荐名称三'},
				{ name : '推荐名称四'},
				{ name : '推荐名称五'}
			],
			range : [
				'1','2','3'
			],
			array : [['a','b'],['网店', '店','铺', '室', '工作室','经营部','服务部','事务所']],
			index: 0,
			canClick : false, // 开始查询 是否能够点击
			inputRed : false, // 字号自我检测有问题的时候标红
		};
	},
	onLoad() {
		var self = this;
		// 首先需要加载经营范围
		try {
			const value = uni.getStorageSync('business_scope');
			if (value) {
				// console.log('111',value);
				self.array[0] = value;
			}
		} catch (e) {
			// error
		}
	},
	methods: {
		change() {
			console.log(11);
		},
		bindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value)
			this.index = e.target.value[1];
		},
		startfind() {
			if(this.canClick) {
				//开始查重 目前是随机的 没有真实的数据
				let num = Math.random();
				if(num > 0.5) {
					// 查重失败的界面
					uni.navigateTo({
						url: '../nameRepeatError/nameRepeatError'
					});	
				} else {
					// 查重成功的界面
					uni.navigateTo({
						url: '../nameRepeatSuccess/nameRepeatSuccess'
					});	
				}
			} else {
				uni.showToast({
					title: '请先填写字号',
					duration: 2000,
					icon :'none'
				});
			}
			
		},
		zihaoBlur(e) {
			console.log('失去焦点',e.detail.value);
			if(e.detail.value != '') {
				// 不等于空 可以点击开始查询
				uni.showLoading({
					title: '处理中'
				});
				if(e.detail.value.indexOf('彬') != -1) {
					this.alertModal = true;
					this.canClick = false;
					this.inputRed = true;
					uni.hideLoading();
				} else {
					this.inputRed = false;
					this.canClick = true;
					uni.hideLoading();
				}
			} else {
				this.canClick = false;
			}
			// 这里需要字号的违规检测
		},
		closeImgAlert() {
			this.alertModal = false;
		},
	}
}
